body {
  color: #bfffd1;
  background-color: #000;
  font-family: Arial, sans-serif;
}

h1 {
  font-size: 42px;
  font-stretch: condensed;
}

h2 {
  color: #6e8f77;
  margin: 12px 0;
}

hr {
  border: none;
  border-top: .5px solid #bfffd1;
  width: 100%;
  margin: 20px auto;
}

.website-container {
  grid-template-columns: 2fr 3fr;
  height: 100vh;
  display: grid;
  overflow: hidden;
}

.logo {
  width: 20%;
}

.text-block-1 p {
  font-size: 20px;
}

.services-container {
  flex-flow: wrap;
  gap: 16px;
  display: flex;
}

.service {
  color: #bfffd1;
  background-color: #111613;
  justify-content: center;
  padding: 6px 8px;
  font-size: 18px;
  display: inline-flex;
}

.call-to-action {
  color: #bfffd1;
  background-color: #111613;
  justify-content: center;
  width: 100%;
  padding: 12px 0;
  transition: background-color .3s, color .3s;
  display: inline-flex;
}

.call-to-action:hover {
  color: #f1faf3;
  background-color: #6e8f77;
}

.left-homepage {
  height: 100vh;
  padding: 24px;
  overflow-y: auto;
}

.right-homepage {
  will-change: transform;
  -ms-overflow-style: none;
  scrollbar-width: none;
  height: 100vh;
  padding: 24px;
  overflow: hidden;
}

.right-homepage::-webkit-scrollbar {
  display: none;
}

.column-1-img {
  flex-flow: wrap;
  gap: 24px;
  padding: 8px 0;
  display: flex;
}

.column-1-img img {
  object-fit: contain;
  width: 100%;
  min-width: 0;
  max-width: 100%;
  height: auto;
}

.column-2-img {
  flex-flow: wrap;
  gap: 24px;
  padding: 8px 0;
  display: flex;
}

.column-2-img img, .column-2-img video {
  object-fit: contain;
  flex: 1;
  width: 100%;
  min-width: 0;
  max-width: 100%;
  height: auto;
}

.column-3-img {
  flex-direction: row;
  flex: 1;
  gap: 24px;
  padding: 8px 0;
  display: flex;
}

.column-3-img img {
  object-fit: contain;
  width: 100%;
  min-width: 0;
  max-width: 100%;
  height: auto;
}

@media screen and (width <= 768px) {
  .website-container {
    grid-template-columns: 1fr;
    height: auto;
    display: grid;
    overflow: visible;
  }

  .left-homepage {
    height: 100%;
  }

  .logo {
    width: 25%;
  }

  h1 {
    font-size: 32px;
  }
}

@media screen and (width >= 769px) and (width <= 1268px) {
  .website-container {
    grid-template-columns: 1fr;
    height: auto;
    display: grid;
    overflow: visible;
  }

  .logo {
    width: 40%;
  }

  h1 {
    font-size: 32px;
  }
}
